import Elements from './Elements';
import tabElements from './data/elements.json';

export default function Liste()
{
    return(
        <ul>
            {tabElements.map(cetElement=>
                <Elements id = {cetElement.id} nom={cetElement.nom} couleur={cetElement.couleur} dateMod={cetElement.dateModification} />
            )}
        </ul>
    );
}